.ModalContainer {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 10002;
    cursor: default;
}

.ModalPortal {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    border-radius: 5px !important;
    overflow: hidden;
    z-index: 10000;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.7);
}

.ModalPortal > .Title {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    height: 10px;
    background-color: #3EB4FE;
    color: white;
    font-size: 14px;
    font-weight: bold;
    padding: 12px 0 12px 12px;
    -webkit-user-select: none;
}

.ModalPortal > .Title > .Close {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-align-items: center;
    font-size: 22px;
    cursor: pointer;
    padding: 20px 4px 20px;
    width: 30px;
    height: 40px;
}


.ModalPortal > .Title > .Close:hover {
    background-color: lightcoral;
}

.ModalPortal > .Content {
    background-color: white;
    padding: 10px 20px 10px 20px;
    width: 300px;
    height: 120px;
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    -webkit-justify-content: space-around;
    align-items: center;
    -webkit-align-items: center;
    flex-direction: column;
    -webkit-flex-direction: column;
    -webkit-user-select: none;
    position: relative;
}

.ModalPortal .ContentData {
    width: 100%;
    max-height: 90px;
    word-break: break-all;
    overflow: hidden;
}

.ModalPortal .foldIcon {
    font-size: 20px;
    cursor: pointer;
}

.ModalPortal .foldIcon.close{
    transform: rotate(90deg);
}

.ModalPortal .Info, .ModalPortal .Warn, .ModalPortal .Error {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-align-items: center;
}

.ModalPortal .Info::before, .ModalPortal .Warn::before {
    content: '!';
    color: white;
    text-align: center;
    font-size: 22px;
    padding-top: 1px;
    margin-right: 5px;
    background: #FCA85D;
    border-radius: 15px;
    min-width: 30px;
    height: 30px;
    font-family: fantasy;
}

.ModalPortal .Error::before {
    content: '\2715';
    color: white;
    text-align: center;
    font-size: 21px;
    margin-right: 5px;
    border-radius: 15px;
    background: #FC5C50;
    min-width: 30px;
    height: 30px;
    font-family: fantasy;
}

.ModalPortal .ButtonOk, .ModalPortal .ButtonsCancel {
    font-size: 14px;
    font-family: 'Microsoft YaHei';
    background-color: white;
    border: 1px solid #dce0e2;
    border-radius: 5px;
    width: 86px;
    height: 32px;
    text-align: center;
    margin-left: 14px;
    outline: none;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    z-index: 1;
}

.ModalPortal .ButtonOk:hover, .ModalPortal .ButtonsCancel:hover {
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

.ModalPortal .ButtonOk {
    background: #3DC826;
    border-color: #3DC826;
    color: #ffffff;
}
.spaceSpan {
    flex: 1;
    -webkit-flex: 1;
}