.syModalContainer {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 10002;
    cursor: default;
}

.syModalPortal {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    border-radius: 3px !important;
    /*overflow: hidden;*/
    z-index: 10000;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.7);
}

.syModalPortal > .syContent {
    background-color: rgba(52, 54, 59, 0);
    padding: 0px 0px 0px 0px;
    width: 380px;
    height: 152px;
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    -webkit-justify-content: space-around;
    align-items: center;
    -webkit-align-items: center;
    flex-direction: column;
    -webkit-flex-direction: column;
    -webkit-user-select: none;
    position: relative;
}